// skew
// @include skew(0deg, 0deg);
@mixin skew($width, $height) {
  transform: skew($width, $height);
  -webkit-transform: skew($width, $height);
  -moz-transform: skew($width, $height);
  -ms-transform: skew($width, $height);
  -o-transform: skew($width, $height);
}

// @include skewX(0deg);
@mixin skewX($width) {
  transform: skewX($width);
  -webkit-transform: skewX($width);
  -moz-transform: skewX($width);
  -ms-transform: skewX($width);
  -o-transform: skewX($width);
}

// @include skewY(0deg, 0deg);
@mixin skewY($height) {
  transform: skewY($height);
  -webkit-transform: skewY($height);
  -moz-transform: skewY($height);
  -ms-transform: skewY($height);
  -o-transform: skewY($height);
}

// $translateX, $translateY, $scaleX, $skewY, $skewX ,$scaleY
// @include matrix( 1, 0, 0, 1, 0, 0 );
@mixin matrix($translateX, $translateY, $scaleX, $skewY, $skewX, $scaleY) {
  transform: matrix($translateX, $translateY, $scaleX, $skewY, $skewX, $scaleY);
  -webkit-transform: matrix(
    $translateX,
    $translateY,
    $scaleX,
    $skewY,
    $skewX,
    $scaleY
  );
  -moz-transform: matrix(
    $translateX,
    $translateY,
    $scaleX,
    $skewY,
    $skewX,
    $scaleY
  );
  -ms-transform: matrix(
    $translateX,
    $translateY,
    $scaleX,
    $skewY,
    $skewX,
    $scaleY
  );
  -o-transform: matrix(
    $translateX,
    $translateY,
    $scaleX,
    $skewY,
    $skewX,
    $scaleY
  );
}

//  box-shadow: horizontal     |  vertical     |  blur | spread | color | inset
//  box-shadow: right + left   |  top + button |  blur | spread | color | inset
//  @include boxShadow(-1px, 0px, 10px, #000);
@mixin boxShadow($right, $button, $spread, $color) {
  -o-box-shadow: $right $button $spread $color;
  -ms-box-shadow: $right $button $spread $color;
  box-shadow: $right $button $spread $color;
}

// hover
// @include hover {}
@mixin hover {
  &:not([disabled]):hover {
    @content;
  }
}

// ul
// @include horizontal-list;
@mixin reset-list {
  margin: 0;
  padding: 0;
  list-style: none;
}

@mixin horizontal-list {
  @include reset-list;
  li {
    display: inline-block;
    margin: {
      left: -2px;
      right: 2em;
    }
  }
}

// media
// @media(max-width:767px){};   xs   || mobile
// @media(min-width:768px){};   sm
// @media(max-width:991 px){};  md
// @media(min-width:992px){};   md
// @media(min-width:1200px){};  lg
// @include xs-media{}
@mixin xs-media {
  @media (max-width: 767px) {
    @content;
  }
}

// @include xs-sm-media{}
@mixin xs-sm-media {
  @media (min-width: 250px) and (max-width: 767px) {
    @content;
  }
}

// @include sm-media{}
@mixin sm-media {
  @media (min-width: 768px) {
    @content;
  }
}

// @include sm-md-media{}
@mixin sm-md-media {
  @media (min-width: 768px) and (max-width: 991px) {
    @content;
  }
}

@mixin md-media {
  @media (min-width: 992px) {
    @content;
  }
}

@mixin md-lg-media {
  @media (min-width: 992px) and (max-width: 1199px) {
    @content;
  }
}

@mixin lg-media {
  @media (min-width: 1200px) {
    @content;
  }
}
@mixin lg-xlg-media {
  @media (min-width: 1200px) and (max-width: 1440px) {
    @content;
  }
}
@mixin xlg-xxlg-media {
  @media (min-width: 1440px) and (max-width: 1566px) {
    @content;
  }
}

// @include mq(767px) {}
// @include mq(1500px, 1900px) {}
@mixin mq($arg...) {
  @if length($arg) ==1 {
    @media screen and (max-width: nth($arg, 1)) {
      @content;
    }
  }
  @if length($arg) ==2 {
    @media screen and (max-width: nth($arg, 1)) and (max-width: nth($arg, 2)) {
      @content;
    }
  }
}

// media b5
@mixin media-b5($point) {
  // 576 to Upper
  @if $point==xs {
    @media (min-width: 360px) {
      @content;
    }
  }
  // 576 to Upper
  @if $point==sm {
    @media (min-width: 576px) {
      @content;
    }
  }
  // 768 to Upper
  @if $point==md {
    @media (min-width: 768px) {
      @content;
    }
  }
  // 998 to Upper
  @if $point==lg {
    @media (min-width: 992px) {
      @content;
    }
  }
  // 1200 to Upper
  @if $point==xlg {
    @media (min-width: 1200px) {
      @content;
    }
  }
  // 1367 to Upper
  @if $point==xxlg {
    @media (min-width: 1566px) {
      @content;
    }
  }
}

// overlay
// @include overlay;
@mixin overlay {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
}

// prefix
// @include prefix(box-shadow, 10px 10px 10px #ccc, webkit moz o ms);
@mixin prefix($property, $value, $prefixes: ()) {
  @each $prefix in $prefixes {
    #{'-' + $prefix + '-' + $property}: $value;
  }
  #{$property}: $value;
}

// opacity
//  @include opacity(0.5);
@mixin opacity($value) {
  zoom: 1;
  $opacity-ie: $value * 100;
  filter: alpha(opacity=$opacity-ie);
  -webkit-filter: alpha(opacity=$opacity-ie);
  opacity: $value;
}

// element center
// @include flexCenter;
@mixin flexCenter {
  display: flex;
  justify-content: center;
  align-items: center;
}

// @include positionCenter;
@mixin positionCenter {
  position: absolute;
  top: 50%;
  left: 50%;
  @include translate(-50%, -50%);
}

// clearfix
// clear float
// @include clearfix;
@mixin clearfix {
  &::after {
    content: "";
    display: block;
    clear: both;
  }
}

// grid
//@include grid-left;
@mixin grid-left($cols, $mgn) {
  float: left;
  margin-right: $mgn;
  margin-bottom: $mgn;
  width: ((100% - (($cols - 1) * $mgn)) / $cols);
  &:nth-child(#{$cols}n) {
    margin-right: 0;
  }
}

